
.customStyle {
  margin-bottom: 70px;
}
.customStyle .error-icon-style {
  color: #bbb;
  font-size: 24px;
  position: absolute;
  z-index: 10;
  top: -10px;
  right: -10px;
}
.customStyle .el-dialog__body {
  padding: 5px 20px !important;
}
.customStyle .customBox {
  background-color: #f9f9f9;
  border: 1px solid #e5e5e5;
  width: 320px;
  min-height: 450px;
  position: relative;
  margin-top: 10px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.customBox .customBoxTop {
  min-height: 304px;
  padding-bottom: 9px;
}
.customStyle .customBoxHead {
  height: 64px;
  line-height: 64px;
  width: 320px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../../../assets/phoneImg/phoneTopCustom.png);
}
.customStyle .customBoxHead .customBoxHeadTitle {
  display: inline-block;
  padding: 12px 0px 0;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  vertical-align: middle;
  color: #fff;
}
.customBox_add--grouped {
  background: #fff;
  padding-bottom: 5px;
  position: relative;
  border-top: 1px solid #e5e5e5;
}
.customBox_add-component {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.customBox_add-component__grouped {
  padding: 0 5px 0 10px;
}
.customBox_add-component__groupedTitle {
  font-size: 12px;
  margin-bottom: 8px;
  margin-top: 13px;
}
.customBox_add-component__grouped-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.add-component__grouped-btn-wrapper {
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 33.33%;
          flex: 0 0 33.33%;
  height: 30px;
  margin-bottom: 5px;
  padding-right: 5px;
}
.add-component__grouped-btn {
  border: 1px dashed #e5e5e5;
  color: #666;
  background: #f8f8f8;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 12px;
  border-radius: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.backgroundCurrent {
  color: #38f;
  background: #cfe1f8;
  border: 1px dashed #e5e5e5;
}
.preview-item {
  position: relative;
}
.preview-item:hover:before {
  z-index: 2;
  display: block;
  border: 1px dashed #38f;
}
/* 右边的设置div */
.zent-design-editor-item {
  padding: 12px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 5;
  min-height: 28px;
  background: #f8f8f8;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  width: 420px;
  margin-left: 20px;
  font-size: 12px;
  position: absolute;
  left: 100%;
  top: 0;
}
.zent-design-editor-item:after,
.zent-design-editor-item:before {
  right: 100%;
  top: 20px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.zent-design-editor-item:after {
  border-color: transparent;
  border-right-color: #f8f8f8;
  border-width: 6px;
  margin-top: -6px;
}
.zent-design-editor-item:before {
  border-color: transparent;
  border-right-color: #e5e5e5;
  border-width: 7px;
  margin-top: -7px;
}
.zent-design-editor-item a {
  color: #38f;
  text-decoration: none;
  cursor: pointer;
}



