/*设置数字输入框*/
.el-input-number.is-controls-right .el-input__inner {
  padding-right: 0;
  padding-left: 15px;
}
.el-input-number .el-input__inner {
  text-align: left;
}
.el-input-number__increase,
.el-input-number__decrease {
  display: none;
}
.el-input-number {
  width: 100px;
}
.vaccaeParamn .el-input-number {
  margin: 0 10px;
}
.vaccaeParamn .el-input {
  width: 250px;
}
.vaccaeParamn .ueditor-style .el-form-item__content {
  line-height: 20px !important;
}
.vaccaeParamn .ueditor-style .edui-arrow {
  width: 12px !important;
}
.vaccaeParamn .MicroAppliy .el-checkbox-group {
  display: inline-block;
  line-height: 20px;
  margin-right: 30px;
}



