
.tasteStyle .imgTipStyle {
  font-size: 10px;
  margin-top: 15px;
  display: inline-block;
}
.tasteStyle .tableAdd .el-input {
  max-width: 250px;
}
.tasteStyle .tableAdd .imgLoadStyle .el-upload--picture-card,
.tasteStyle .tableAdd .imgLoadStyle .el-upload-list__item {
  width: 70px;
  height: 70px;
}
.tasteStyle .tableAdd .imgLoadStyle .el-upload--picture-card {
  line-height: 70px;
}
.tasteStyle .tableAdd .imgLoadStyle .avatar-uploader-icon {
  line-height: 70px !important;
  width: 70px !important;
  height: 70px !important;
}
.tasteStyle .tableAdd .imgStyle {
  width: 70px;
  height: 70px;
  border: 1px solid #eee;
}
.tasteStyle .tableAdd .imgStyle img {
  width: 100%;
  height: 100%;
}
.tasteStyle .lineBack {
  width: 100%;
  height: 1px;
  background: #d6d0d0;
  margin: 15px 0;
}
.tasteStyle .labelImgStyle {
  width: 40px;
  height: 40px;
  margin-right: 8px;
  display: inline-block;
}
.tasteStyle .labelImgStyle img {
  width: 100%;
  height: 100%;
}
.tasteStyle .labelImgStyleWidth {
  width: 60px;
  height: 60px;
  margin-bottom: 5px;
  position: relative;
}
.tasteStyle .labelImgStyleWidth .icon {
  position: absolute;
  top: -1px;
  right: 0;
  width: 100%;
  text-align: center;
  line-height: 60px;
  background: #ccc;
}
.tasteStyle .tableLabelImgStyle {
  border: 1px solid #eee;
}
.tasteStyle .CustomImgStyle .el-upload--picture-card,
.tasteStyle .CustomImgStyle .el-upload-list__item {
  width: 80px;
  height: 80px;
}
.tasteStyle .CustomImgStyle .el-upload--picture-card {
  line-height: 80px;
}



