
.loginContent {
  width: 100%;
  height: 100vh;
  background: url("../../assets/img/pin1.jpg") no-repeat;
  background-size: cover;
}
.loginContent .loginGuard {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.loginContent .demo-ruleForm {
  position: absolute;
  margin: auto;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding-top: 50px;
  width: 500px;
  border: 2px solid #eee;
  height: 300px;
  text-align: center;
}
.loginContent .demo-ruleForm .btnStyle {
  width: 300px;
  border-radius: 22px;
}
.loginContent .demo-ruleForm .el-form-item {
  width: 400px;
}
.loginContent .demo-ruleForm .el-input__inner {
  border-radius: 22px;
  background: transparent;
  color: #fff;
}



