
.addEditIntegral .el-input {
  width: 300px;
}
.addEditIntegral .el-input-number .el-input {
  width: auto;
}
.addEditIntegral .FeaturesStyle .el-form-item__content {
  line-height: 20px;
}
.addEditIntegral .preserveStyle {
  position: fixed;
  background: #fff;
  width: 100%;
  text-align: center;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
  bottom: 0;
  z-index: 1100;
  padding: 15px 0;
}
.addEditIntegral .preserveStyle button {
  -webkit-transform: translateX(-80px);
          transform: translateX(-80px);
}



