/* zent-design-editor-item都套一個 */
.styleFlex .el-form-item__content {
  line-height: 0 !important;
}
.zent-design-editor-item .rc-design-editor-component-title {
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 1px solid #e5e5e5;
}
.zent-design-editor-item .el-radio__label {
  font-size: 12px;
}
.el-form-item {
  margin-bottom: 20px !important;
}
.zent-design-editor-item a {
  color: #38f;
  text-decoration: none;
  cursor: pointer;
}
.zent-design-editor-item .el-form-item__label,
.zent-design-editor-item .el-form-item__content {
  font-size: 12px;
}
.el-form-item__content {
  font-size: 12px;
}
.el-radio {
  line-height: normal;
}
/* //滑块 */
.el-slider__runway {
  margin: 0;
}
.el-slider__button {
  width: 10px;
  height: 10px;
}
.styleFlex .el-form-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.styleFlex .el-form-item__content {
  margin-left: 0 !important;
}
/* //颜色选择器 */
.el-color-dropdown__link-btn {
  display: none !important;
}
.btnReset {
  margin-left: 5px;
}
.height-slider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.el-radio {
  font-size: 12px;
}
.magicCubeFun .el-form-item__label {
  padding: 0 7px 0 0 !important;
}
.el-form-item__label {
  color: #000;
}
.el-dropdown-menu {
  max-height: 300px;
  overflow-y: scroll;
}
/* //魔方选择 */
.rc-design-select-templates {
  width: 100px;
  height: 100px;
  display: inline-block;
  border: 1px solid #e5e5e5;
  margin: 0 7px 15px 0;
  padding-top: 5px;
  background-color: #fff;
  text-align: center;
  cursor: pointer;
}
.rc-design-select-templates.active {
  border-color: #38f;
}
.rc-design-select-templates__image-block {
  width: 100px;
  height: 64px;
}
/* 数字计步器 */
.numInput.is-controls-right .el-input-number__increase,
.numInput.is-controls-right .el-input-number__decrease {
  display: none;
}
.numInput.is-controls-right .el-input__inner {
  padding-left: 0px;
  padding-right: 0;
}
.sliderAlign .el-form-item__content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
/*虚线框*/
.rc-design-editor-card-item {
  position: relative;
  background-color: #fff;
  border: 1px dashed #e5e5e5;
  margin: 10px 0;
  padding: 10px;
}
.rc-design-editor-card-add-icon {
  margin-right: 10px;
  color: #38f;
  font-weight: 700;
}
.cardEelete {
  position: absolute;
  font-size: 24px;
  top: -12px;
  right: -12px;
  color: rgba(0, 0, 0, 0.3) !important;
  cursor: pointer;
}
.styleFlex .el-form-item__content {
  line-height: 0 !important;
}
.search-component-editor-card-tip {
  margin-bottom: 12px;
  color: #666;
  margin-top: 10px;
}
.rc-design-editor-card {
  position: relative;
}
/* .rc-design-editor-card-item {
  position: relative;
  background-color: #fff;
  border: 1px dashed #e5e5e5;
  margin: 10px 0;
  padding: 10px;
} */
.rc-design-editor-card-add {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
  margin: 10px 0;
  padding: 10px;
  border: 1px dashed #e5e5e5;
  cursor: pointer;
  color: #38f;
}
.rc-design-editor-card-add:last-child {
  margin-bottom: 0;
}
.rc-design-editor-card-add:first-child {
  margin-top: 0;
}
/* .rc-design-editor-card-add-icon {
  margin-right: 10px;
  color: #38f;
  font-weight: 700;
} */
.search-component-search-style-wrapper {
  margin-top: 10px;
  padding-top: 20px;
  border: none;
}
.controls-card {
  padding: 10px 0;
  background: #fff;
}
.rc-design-component-search-editor__height-slider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.btnReset {
  vertical-align: middle;
  margin-left: 6px;
}



