
@font-face {font-family: "iconfont";
  src: url('assets/icon/iconfont.eot?t=1587100536798'); /* IE9 */
  src: url('assets/icon/iconfont.eot?t=1587100536798#iefix') format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url('assets/icon/iconfont.woff?t=1587100536798') format('woff'),
  url('assets/icon/iconfont.ttf?t=1587100536798') format('truetype'), 
  url('assets/icon/iconfont.svg?t=1587100536798#iconfont') format('svg'); /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-sousuo:before {
  content: "\e651";
}
.icon-jiantou:before {
  content: "\e603";
}
.icon-yinpinbofang:before {
  content: "\e666";
}
.icon-zanting:before {
  content: "\e667";
}
.icon-dianpu:before {
  content: "\e600";
}
.marginBottom {
  margin-bottom: 20px !important;
}
.el-range-separator {
  width: 7% !important;
}
.smallRoutine {
  width: 200px;
  min-height: 220px;
  text-align: center;
  margin: 0 15px;
}
.smallRoutine .smallRoutineTop {
  font-size: 16px;
  margin-bottom: 10px;
}
.smallRoutine .smallRoutineTop .close {
  position: absolute;
  top: 4px;
  right: 15px;
  font-size: 25px;
  cursor: pointer;
}
.smallRoutine .smallRoutineUpDown {
  margin-top: 8px;
  display: inline-block;
}
::-webkit-scrollbar {
  width: 4px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
          box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar-track {
  border-radius: 0;
  background: rgba(0, 0, 0, 0.1);
}
.el-message {
  z-index: 5000 !important;
}
body {
  background-color: #fff;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif, "12px Extra Small";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 100%;
}
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
p {
  line-height: initial;
}
input {
  background: none;
  outline: none;
  border: none;
}
.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
div::-webkit-scrollbar {
  /*滚动条整体样式*/
  height: 10px;
}
.preserveStyle {
  position: fixed;
  background: #fff;
  width: 100%;
  text-align: center;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
  bottom: 0;
  z-index: 1100;
  padding: 15px 0;
}
.preserveStyle button {
  -webkit-transform: translateX(-80px);
          transform: translateX(-80px);
}
.m-colorPicker .box {
  width: 214px !important;
}
.m-colorPicker .hd .colorView {
  width: 30px !important;
}
.m-colorPicker .colorBtn {
  width: 30px !important;
  height: 30px !important;
}



