
.goodsSelect .el-dialog__body {
  text-align: center;
  padding: 10px 60px;
}
.goodsSelect .el-table__row {
  cursor: pointer;
}
.goodsSelect .el-input {
  width: 300px;
}
.goodsSelect .goodsSelectRow {
  margin: 30px 0 !important;
}
.goodsSelect .goodsSelectRow .leftGoods,
.goodsSelect .goodsSelectRow .rightGoods {
  height: 400px;
  border: 1px solid #e5e5e5;
  padding: 0 !important;
}
.goodsSelect .goodsSelectRow .leftGoodsHead {
  height: 40px !important;
  line-height: 40px;
  background: #eeeeee;
}
.goodsSelect .goodsSelectRow .GoodsScroll {
  /*padding-top:4px;*/
  height: 358px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.goodsSelect .goodsSelectRow .rightGoods li {
  list-style: none;
  text-align: left;
  line-height: 30px;
  margin-left: 20px;
}
.goodsSelect .goodsSelectRow .rightGoods .GoodsScrollName {
  height: 358px;
  padding: 20px 0 20px 0;
}
.goodsSelect .goodsSelectRow .rightGoods .scroll {
  overflow-y: scroll;
}
.goodsSelect .goodsSelectRow .rightGoods .el-table th {
  color: #606266;
  font-weight: 400;
  background: #eee;
  text-align: center;
  padding: 0;
  line-height: 40px;
}
.goodsSelect .goodsSelectRow .rightGoods .el-table td {
  padding: 10px;
}
.goodsSelect .el-table__body-wrapper {
  height: 358px;
  overflow-y: scroll;
}



