
.addEditGoods .preserveStyle {
  position: fixed;
  background: #fff;
  width: 100%;
  text-align: center;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
  bottom: 0;
  z-index: 1100;
  padding: 15px 0;
}
.addEditGoods .preserveStyle button {
  -webkit-transform: translateX(-80px);
          transform: translateX(-80px);
}
.addEditGoods .el-input,
.addEditGoods .el-select,
.addEditGoods .el-textarea {
  width: 300px;
}
.addEditGoods .el-input-number .el-input,
.addEditGoods .el-date-range-picker .el-input {
  width: auto;
}
.addEditGoods .FeaturesStyle .el-form-item__content {
  line-height: 20px;
}
.addEditGoods {
  margin-bottom: 80px;
  min-width: 1230px;
}
.addEditGoods .takeDateBool .el-input {
  width: 150px;
}
.addEditGoods .areaTree .el-dialog__body {
  /*overflow-y: scroll;*/
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  /*height: 500px;*/
  padding-top: 15px;
}
.addEditGoods .TreeShop .el-tree-node__label {
  margin-left: 10px !important;
}
.addEditGoods .TicketDialog .el-checkbox {
  display: block;
  margin-top: 8px;
}
.addEditGoods .FeaturesStyle .el-form-item__content {
  line-height: 20px;
}
.addEditGoods .goods_normsLabel {
  display: inline-block;
  text-align: right;
  padding-right: 12px;
  font-size: 14px;
  color: #606266;
  width: 150px;
  float: left;
  line-height: 40px;
}
.addEditGoods .goods_norms {
  display: inline-block;
  padding: 20px;
  width: 80%;
  margin-bottom: 20px;
  border: 1px solid silver;
}
.addEditGoods .goods_norms .el-select__input {
  margin-left: 4px;
}
.addEditGoods .goods_norms .el-form-item__label {
  text-align: left;
  width: 120px !important;
}
.addEditGoods .goods_norms .goods_norms_row {
  margin-bottom: 20px;
}
.addEditGoods .goods_norms .goods_norms_row .el-form-item {
  display: inline-block;
}
.addEditGoods .goods_norms .goods_norms_row .el-form-item .el-form-item__content {
  margin-left: 0 !important;
}
.addEditGoods .goods_norms .goods_norms_row .el-input-group__prepend {
  padding: 0 10px;
}
.addEditGoods .goods_norms .goods_norms_row .el-input__inner {
  vertical-align: middle;
}
.addEditGoods .goods_norms .goods_norms_row .iconError {
  font-size: 25px;
  vertical-align: middle;
  color: rgba(153, 153, 153, 0.6);
  margin-left: 20px;
}
.addEditGoods .goods_norms .goods_norms_row .ImgLoadStyle {
  display: inline-block;
  vertical-align: middle;
  /*width: 100px;*/
}
.addEditGoods .goods_norms .goods_norms_row .ImgLoadStyle .el-upload--picture-card {
  /*width: 100%;*/
  height: 90px;
  width: 90px;
  line-height: 90px;
}
.addEditGoods .goods_norms .goods_norms_row .ImgLoadStyle .el-upload--picture-card .el-upload-list__item {
  line-height: 65px !important;
  height: 90px;
  width: 90px;
}
.addEditGoods .goods_norms .goods_norms_row .ImgLoadStyle .el-upload-list--picture-card .el-upload-list__item {
  height: 90px;
  width: 90px;
  margin: 0 8px -3px 0 !important;
}
.addEditGoods .goods_norms .goods_norms_row .el-select {
  width: auto;
}
.addEditGoods .goods_norms .el-input {
  width: 120px !important;
  vertical-align: middle;
}
.addEditGoods .goods_norms .tasteInputNorms .el-input {
  min-width: 120px !important;
  width: auto !important;
}
.addEditGoods .FinHourInput {
  display: inline-block;
}
.addEditGoods .FinHourInput .el-input,
.addEditGoods .FinHourInput .el-select {
  width: 120px !important;
}



