
.wz-hover{padding:25px 80px;display:inline-block;margin:15px 30px;border:3px solid #fff;position:relative;background:0 0;color:#fff;-webkit-transition:all .3s;transition:all .3s
}
.wz-hover:after{content:'';position:absolute;z-index:-1;background:#fff;-webkit-transition:all .3s;transition:all .3s
}
.wz-hover:focus{-webkit-box-shadow:none;box-shadow:none;outline:0
}
.wz-hover-height-full:after{width:100%;height:0;top:0;left:0
}
.wz-hover-height-full:hover{color:#0e83cd
}
.wz-hover-height-full:hover:after{height:100%
}
.wz-hover-width-full:after{width:0;height:100%;top:0;left:0;background:#fff
}
.wz-hover-width-full:active,.wz-hover-width-full:hover{color:#0e83cd
}
.wz-hover-width-full:active:after,.wz-hover-width-full:hover:after{width:100%
}
.wz-hover-width-half{overflow:hidden
}
.wz-hover-width-half:after{width:0;height:103%;top:50%;left:50%;background:#fff;opacity:0;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%)
}
.wz-hover-width-half:active,.wz-hover-width-half:hover{color:#0e83cd
}
.wz-hover-width-half:hover:after{width:90%;opacity:1
}
.wz-hover-width-half:active:after{width:101%;opacity:1
}
.wz-hover-height-half{overflow:hidden
}
.wz-hover-height-half:after{width:101%;height:0;top:50%;left:50%;background:#fff;opacity:0;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%)
}
.wz-hover-height-half:active,.wz-hover-height-half:hover{color:#0e83cd
}
.wz-hover-height-half:hover:after{height:75%;opacity:1
}
.wz-hover-height-half:active:after{height:130%;opacity:1
}
.wz-hover-lean{overflow:hidden
}
.wz-hover-lean:after{width:100%;height:0;top:50%;left:50%;background:#fff;opacity:0;-webkit-transform:translateX(-50%) translateY(-50%) rotate(45deg);transform:translateX(-50%) translateY(-50%) rotate(45deg)
}
.wz-hover-lean:active,.wz-hover-lean:hover{color:#0e83cd
}
.wz-hover-lean:hover:after{height:260%;opacity:1
}
.wz-hover-lean:active:after{height:400%;opacity:1
}
.wz-hover-arrow{border-radius:50px;border:3px solid #fff;color:#fff;overflow:hidden
}
.wz-hover-arrow:active{border-color:#17954c;color:#17954c
}
.wz-hover-arrow:hover{background:#24b662
}
.wz-hover-arrow:before{position:absolute;height:100%;font-size:12px;line-height:5;color:#fff;-webkit-transition:all .3s;transition:all .3s
}
.wz-hover-arrow:active:before{color:#17954c
}
.wz-hover-arrow-right:before{left:130%;top:0
}
.wz-hover-arrow-right:hover:before{left:80%
}
.wz-hover-arrow-left:before{left:-50%;top:0
}
.wz-hover-arrow-left:hover:before{left:10%
}
.wz-hover-change{border:0;background:#823aa0;color:#fff;height:70px;min-width:260px;line-height:24px;font-size:16px;overflow:hidden;-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.wz-hover-change:active{background:#9053a9;top:2px
}
.wz-hover-change span{display:inline-block;width:100%;height:100%;-webkit-transition:all .3s;-webkit-backface-visibility:hidden;-moz-transition:all .3s;-moz-backface-visibility:hidden;transition:all .3s;backface-visibility:hidden
}
.wz-hover-change:before{position:absolute;height:100%;width:100%;line-height:2.5;font-size:180%;-webkit-transition:all .3s;transition:all .3s
}
.wz-hover-change:active:before{color:#703b87
}
.wz-hover-change-top:hover span{-webkit-transform:translateY(300%);transform:translateY(300%)
}
.wz-hover-change-top:before{left:0;top:-100%
}
.wz-hover-change-top:hover:before{top:0
}
.wz-hover-change-left:hover span{-webkit-transform:translateX(200%);transform:translateX(200%)
}
.wz-hover-change-left:before{left:-100%;top:0
}
.wz-hover-change-left:hover:before{left:0
}
.wz-coolhover span:after,.wz-coolhover span:before,.wz-coolhover:after,.wz-coolhover:before{z-index:-1
}
.centerer{width:100%;max-width:600px;margin:0 auto
}
.wrap{width:50%;float:left
}
