@-webkit-keyframes criss-cross-left{
0%{left:-20px
}
50%{left:50%;width:20px;height:20px
}
100%{left:50%;width:375px;height:375px
}
}
@keyframes criss-cross-left{
0%{left:-20px
}
50%{left:50%;width:20px;height:20px
}
100%{left:50%;width:375px;height:375px
}
}
@-webkit-keyframes criss-cross-right{
0%{right:-20px
}
50%{right:50%;width:20px;height:20px
}
100%{right:50%;width:375px;height:375px
}
}
@keyframes criss-cross-right{
0%{right:-20px
}
50%{right:50%;width:20px;height:20px
}
100%{right:50%;width:375px;height:375px
}
}
.wz-coolhover-collision{position:relative;color:#9a7cba
}
.wz-coolhover-collision:after,.wz-coolhover-collision:before{position:absolute;top:50%;content:'';width:20px;height:20px;background:#8865ae;border-radius:50%
}
.wz-coolhover-collision:before{left:-20px;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.wz-coolhover-collision:after{right:-20px;-webkit-transform:translate(50%,-50%);transform:translate(50%,-50%)
}
.wz-coolhover-collision:hover:before{-webkit-animation:criss-cross-left .8s both;animation:criss-cross-left .8s both
}
.wz-coolhover-collision:hover:after{-webkit-animation:criss-cross-right .8s both;animation:criss-cross-right .8s both
}
.fade{opacity:0;-webkit-transition:opacity .15s linear;transition:opacity .15s linear
}
.fade.show{opacity:1
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: .875rem !important;
  font-weight: 400;
  line-height: 1.5;
  color: #263238;
  background-color: #fff;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%
}
a {
  text-decoration: none;
}/*正常的未被访问过的链接*/
a:link {
  text-decoration: none;
}/*已经访问过的链接*/
a:visited {
  text-decoration: none;
}/*鼠标划过(停留)的链接*/
a:hover {
  text-decoration: none;
}/* 正在点击的链接，鼠标在元素上按下还没有松开*/
a:active {
  text-decoration: none;
}/* 获得焦点的时候 鼠标松开时显示的颜色*/
a:focus {
  text-decoration: none;
}
h1 {
  font-size: 2em;
  margin: .67em 0
}
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: .5rem
}
p {
  /* margin-top: 1rem;
  margin-bottom: 1rem */
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  /* margin-bottom: .5rem; */
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit
}
.h1, h1 {
  font-size: 2.5rem
}
.h2, h2 {
  font-size: 2rem
}
.h3, h3 {
  font-size: 1.75rem
}
.h4, h4 {
  font-size: 1.5rem
}
.h5, h5 {
  font-size: 1.25rem
}
.h6, h6 {
  font-size: 1rem
}
article, aside, footer, header, nav, section {
  display: block
}
img {
  vertical-align: middle;
  border-style: none
}
