
.mallGoods .goodsInfo {
  width: 75px;
  height: 75px;
  border: 1px solid #eee;
}
.mallGoods .goodsInfo img {
  width: 100%;
  height: 100%;
}
.mallGoods .mallGoodsDialog .el-dialog__body {
  padding: 10px 25px 30px !important;
}
.mallGoods .leftGoods,
.mallGoods .rightGoods {
  overflow-y: scroll;
  max-height: 400px;
  border: 1px solid #e5e5e5;
  padding: 0 !important;
}
.mallGoods .leftGoodsHead {
  height: 40px !important;
  line-height: 40px;
  background: #eeeeee;
}
.mallGoods .GoodsScroll {
  /*padding-top:4px;*/
  height: 358px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.mallGoods .rightGoods li {
  list-style: none;
  text-align: left;
  line-height: 30px;
  margin-left: 20px;
}
.mallGoods .rightGoods .GoodsScrollName {
  height: 358px;
  padding: 20px 0 20px 0;
}
.mallGoods .rightGoods .scroll {
  overflow-y: scroll;
}
.mallGoods .rightGoods .el-table th {
  color: #606266;
  font-weight: 400;
  background: #eee;
  text-align: center;
  padding: 0;
  line-height: 40px;
}
.mallGoods .rightGoods .el-table td {
  padding: 10px;
}



