
img {
    max-width: 100%;
    position: relative;
    vertical-align: middle;
    border: 0;
    display: inline-block;
}
img:before{
    position: absolute;
    display: block;
    content: "";
    top: -7px;
    left: -7px;
    height: calc(100% + 10px);
    width: calc(100% + 10px);
    background-color: #e6e6e6;
    border: 2px dotted #c8c8c8;
    border-radius: 5px;
}
img:after{
    position: absolute;
    display: block;
    content: "(>_<)";
    top: 50%;
    left: 50%;
    margin-top: -.5em;
    margin-left: -1.5em;
    height: 1em;
    line-height: 1em;
    width: 3em;
    color: #646464;
    text-align: center;
}
.cap-audio-animation-static,
.cap-audio-bar {
  background: url(../../../assets/phoneImg/sprite_v5.png) no-repeat scroll 0 0;
  background-size: 400px 175px;
}
.cap-slider__finished-portion,
.cap-slider__loaded-portion {
  border-radius: 4.5px;
  height: 100%;
  position: absolute;
  left: 0;
}
.cap-audio-info {
  /* transform: translateY(50%);
  height: 100%; */
}
.cap-audio {
  padding: 15px;
}
.cap-audio-weixin {
  position: relative;
  min-height: 40px;
}
.cap-audio-logo {
  width: 40px;
  height: 40px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
}
.cap-audio-bar {
  width: 185px;
  height: 42px;
  display: inline-block;
  position: absolute;
  left: 50px;
  cursor: pointer;
}
.cap-audio-animation {
  position: absolute;
  left: 21px;
  top: 12px;
  width: 13px;
  height: 17px;
  z-index: 2;
}
.cap-audio-animation-static {
  background-position: -180px -105px;
  width: 13px;
  height: 17px;
  display: block;
  position: absolute;
  left: 21px;
  top: 12px;
  z-index: 1;
}
.cap-audio-loading {
  position: absolute;
  right: 10px;
  top: 12px;
}
.cap-slider__bar {
  position: relative;
  background: #cacaca;
}
.cap-audio--music {
  position: relative;
  padding: 0 15px 0 70px;
  width: 100%;
  height: 74px;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.cap-audio--music .cap-audio-btn {
  position: absolute;
  left: 15px;
  top: 17px;
  font-size: 40px;
  color: #4b0;
}
.cap-audio--music .cap-audio-title {
  padding-right: 15px;
  height: 38px;
  line-height: 38px;
  font-size: 14px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 0;
}
.cap-audio--music .cap-audio-loading {
  top: 10px;
  right: 20px;
}
.cap-audio--music .cap-slider {
  background-color: #f2f2f2;
}
.cap-audio--music .cap-slider__bar {
  margin: 0 8px 0 0;
  height: 2px;
  border-radius: 0;
  background-color: #f2f2f2;
}
.cap-audio--music .cap-slider__finished-portion {
  border-radius: 0;
}
.cap-slider__pivot {
  /* 进度条的圆坨坨 */
  position: absolute;
  top: 50%;
  width: 16px;
  height: 16px;
  border: 3px solid #fff;
  -webkit-box-shadow: 0 1px 4px;
  box-shadow: 0 1px 4px;
  border-radius: 50%;
  background-color: #cacaca;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
}
.cap-audio--music .cap-slider__pivot:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 4px;
  height: 4px;
  border-radius: 4px;
  background: #4b0;
}
.cap-audio--music .cap-slider__pivot {
  width: 14px;
  height: 14px;
  border: 1px solid #e5e5e5;
  -webkit-box-shadow: none;
          box-shadow: none;
}
/* audio */
.cap-audio {
  padding: 15px;
}
.cap-audio--simple {
  position: relative;
  padding: 0 15px 0 50px;
  height: 46px;
  background-color: #fff;
}
.cap-audio--simple-box {
  position: absolute;
  width: 50px;
  height: 46px;
  left: 0;
}
.cap-audio--simple-circle {
  position: absolute;
  width: 25px;
  height: 25px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
}
.cap-audio--simple-btn {
  width: 25px;
  height: 25px;
  background-size: contain;
  background-position: 100%;
  background-repeat: no-repeat;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.imgPaly3 {
  background-image: url(../../../assets/phoneImg/paly3.png);
}
.cap-audio--simple-title-wrapper {
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.cap-audio--simple-title {
  vertical-align: middle;
  display: inline-block;
  font-size: 14px;
  color: #111;
  line-height: 46px;
}
/* //1样式气泡位置 */
.cap-audio-weixin--right .cap-audio-logo {
  float: right;
}
.cap-audio-weixin--right .cap-audio-bar {
  background-position: -187px 0;
  left: auto;
  right: 50px;
}
.cap-audio-weixin--right .cap-audio-bar .cap-audio-animation {
  right: 21px;
  left: auto;
}
.cap-audio-weixin--right .cap-audio-animation-static {
  background-position: -180px -83px;
  right: 21px;
  left: auto;
}
.cap-audio-weixin--right .cap-audio-bar .cap-audio-loading {
  position: absolute;
  left: 20px;
  right: auto;
}



