
.modifyCate .goodsSelectRow {
  margin: 0 30px !important;
  /*margin:30px 0 !important;*/
}
.modifyCate .goodsSelectRow .leftGoods,
.modifyCate .goodsSelectRow .rightGoods {
  height: 440px;
  border: 1px solid #e5e5e5;
  padding: 0 !important;
  overflow: hidden;
}
.modifyCate .goodsSelectRow .leftGoods .el-table__body-wrapper,
.modifyCate .goodsSelectRow .rightGoods .el-table__body-wrapper {
  overflow-y: scroll;
  height: 350px;
}
.modifyCate .goodsSelectRow .rightGoods .el-form-item {
  padding-right: 20px;
  margin-top: 20px;
}
.modifyCate .goodsSelectRow .rightGoods .el-upload--picture-card,
.modifyCate .goodsSelectRow .rightGoods .el-upload-list__item {
  width: 100px;
  height: 100px;
}
.modifyCate .goodsSelectRow .el-upload--picture-card {
  line-height: 100px;
}
.modifyCate .goodsSelectRow .leftGoodsHead {
  background: #f6f6f6;
  line-height: 40px;
  height: 40px !important;
  text-align: center;
}
.modifyCate .goodsSelectRow .modifyCateTable .imgStyle {
  width: 60px;
  height: 60px;
  border: 1px solid #eee;
}
.modifyCate .goodsSelectRow .modifyCateTable .imgStyle img {
  width: 100%;
  height: 100%;
}
.modifyCate .goodsSelectRow .scrollTable {
  height: 398px;
  overflow-y: scroll;
}



