}
.auto-goods .checkboxGroupStyle .el-checkbox {
  display: block;
}
.auto-goods .checkboxGroupStyle .el-radio {
  font-size: 12px;
  margin-right: 10px;
  margin-bottom: 15px;
}
.auto-goods .checkboxGroupStyle .imgLoad-style .el-upload--picture-card {
  width: 50px;
  height: 50px;
  line-height: 50px;
}
.auto-goods .controls-card {
  padding: 10px 0;
  margin-bottom: 20px;
  border: 1px solid #e5e5e5;
  background: #fff;
}
.auto-goods .add-good .el-form-item {
  margin-top: 0px !important;
}
.auto-goods .add-good .rc-design-editor-card-add__inline {
  position: relative;
  float: left;
  width: 50px;
  height: 50px;
  cursor: pointer;
  margin: 0 10px 10px 0;
  display: block;
  border: 1px dashed #ddd;
  background-color: #fff;
}
.auto-goods .add-good .rc-design-editor-card-add__inline .rc-design-editor-card-add-icon {
  position: absolute;
  margin: 0;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3D(-50%, -50%, 0);
          transform: translate3D(-50%, -50%, 0);
}
.auto-goods .add-good .zenticon {
  display: inline-block;
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  font-variant: normal;
  text-rendering: auto;
  text-decoration: inherit;
  line-height: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}



