/*设置数字输入框*/
.el-input-number.is-controls-right .el-input__inner {
  padding-right: 0;
  padding-left: 15px;
}
.el-input-number .el-input__inner {
  text-align: left;
}
.el-input-number__increase,
.el-input-number__decrease {
  display: none;
}
.el-input-number {
  width: 100px;
}
.paymentRule .el-input,
.paymentRule .el-select {
  width: 300px;
}
.paymentRule .el-date-editor .el-range-separator {
  width: 14%;
}
.paymentRule .el-input-number {
  width: auto;
}



