
.popoverLi {
  text-align: center;
}
.popoverLi li {
  line-height: 30px;
  cursor: pointer;
  /*加省略号*/
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.popoverLi li:nth-child(2n) {
  border-top: 1px solid #e5e5e5;
}
.el-popover {
  min-width: 0 !important;
}
.menuBar {
  min-width: 1000px;
}
.menuBar .el-form-item__error {
  width: 300px;
}
.menuBar .menuBarTip {
  background: #fff;
  padding: 20px;
  margin-bottom: 30px;
}
.menuBar .menuBarTip span {
  display: inline-block;
  width: 70%;
}
.menuBar .menuBarLeft {
  min-height: 200px;
  width: 342px;
  border-radius: 20px;
  border: 1px solid #e5e5e5;
  background: #fff;
  margin-bottom: 30px;
  float: left;
}
.menuBar .menuBarLeft .menuBarLeftTop {
  background: url(../../../assets/img/iphone_head.png) no-repeat center center;
  height: 70px;
}
.menuBar .menuBarLeft .menuBarLeftCenter {
  height: 450px;
  border: 1px solid #c5c5c5;
  margin: 10px;
  position: relative;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterTop {
  background: url(../../../assets/img/titlebar.png) no-repeat;
  height: 64px;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  border-top: 1px solid #e5e5e5;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom .CenterBottomLeft {
  width: 15%;
  height: 100%;
  float: left;
  background: url(../../../assets/img/showmenu_icon@2x.png) no-repeat center;
  background-size: 80%;
  cursor: pointer;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom .borderRight {
  border-right: 1px solid #e5e5e5;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom .CenterBottomRight {
  height: 100%;
  line-height: 50px;
  float: right;
  width: 85%;
  /*display: flex;*/
  /*justify-content:space-between;*/
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom .CenterBottomRight li {
  /*flex-grow:1;*/
  height: 100%;
  display: inline-block;
  border-left: 1px solid #e5e5e5;
  text-align: center;
  cursor: pointer;
}
.menuBar .menuBarLeft .menuBarLeftCenter .CenterBottom .CenterBottomRight .el-popover__reference {
  width: 100%;
  display: inline-block;
}
.menuBar .menuBarLeft .menuBarLeftBottom {
  height: 70px;
  text-align: center;
}
.menuBar .menuBarLeft .menuBarLeftBottom:after {
  content: '';
  display: inline-block;
  width: 60px;
  height: 60px;
  border: 1px solid #e5e5e5;
  border-radius: 50%;
}
.menuBar .menuBarRight {
  border: 1px solid #e5e5e5;
  margin-left: 25px;
  float: left;
  width: 60%;
  padding: 25px;
  background: #ffffff;
  border-radius: 20px;
}
.menuBar .menuBarRight .menuBarOne {
  border: 1px solid #e5e5e5;
  padding: 10px;
  margin-bottom: 20px;
  position: relative;
}
.menuBar .menuBarRight .menuBarOne .delIcon {
  position: absolute;
  font-size: 24px;
  color: rgba(0, 0, 0, 0.5);
  top: -12px;
  right: -12px;
  cursor: pointer;
}
.menuBar .menuBarRight .el-form-item {
  display: inline-block;
  margin: 0;
}
.menuBar .menuBarRight .menuBarRightH1 {
  font-size: 18px;
  font-weight: bold;
}
.menuBar .menuBarRight .menuBarRightOne {
  background: #F8F8F8;
  margin: 10px 0 20px 0;
  padding: 0 15px;
}
.menuBar .menuBarRight .menuBarRightOne .el-input {
  width: 120px;
}
.menuBar .menuBarRight .menuBarRightOne .changeType .el-form-item__content {
  margin-left: 20px !important;
}
.menuBar .menuBarRight .menuBarRightOne .el-dropdown-link {
  cursor: pointer;
  color: #409EFF;
  margin-left: 20px;
}
.menuBar .menuBarRight .menuBarRightOne .Keyword {
  margin: 19px 0;
}
.menuBar .menuBarRight .menuBarRightOne .tipTitle {
  color: #999;
  margin-left: 20px;
}
.menuBar .menuBarRight .menuBarRightTwo {
  margin: 10px 0 20px 20px;
}
.menuBar .menuBarRight .menuBarRightTwo .menuBarRightH2 {
  font-size: 16px;
  font-weight: bold;
}
.menuBar .menuBarRight .menuBarRightTwo .menuBarRightOne {
  position: relative;
  /*.delIconChild{*/
  /*  position:absolute;*/
  /*}*/
}
.menuBar .menuBarRight .AddList {
  text-align: center;
  width: 100%;
  height: 45px;
  line-height: 45px;
  border: 1px dashed #ccc;
  cursor: pointer;
}
.menuBar .menuBarRight .AddList .el-icon-circle-plus {
  font-size: 16px;
  color: #409EFF;
}



